import axios, { AxiosRequestConfig } from 'axios';
// import history from '../routes/history';
import { splitUrl } from './utils';

// Get the current location.
// const location = history.location;
const codeMessage = {
  200: '',
  201: '',
  202: '',
  204: '',
  400: '',
  401: '',
  403: '',
  404: '',
  406: '',
  410: '',
  422: '',
  500: '',
  502: '',
  503: '',
  504: '',
};

export interface Options extends AxiosRequestConfig {
  body?: any;
}

/**
 * Requests a URL, returning a promise.
 *
 * @param  {string} url       The URL we want to request
 * @param  {object} [options] The options we want to pass to "fetch"
 * @return {object}           An object containing either "data" or "err"
 */
export default function request(url: string, options: Options = {}) {
  const method = options.method || 'GET';
  const newOptions: Options = {
    url,
    method,
    data: options.body,
    headers: {
      'Content-Type': 'application/json; charset=utf-8',
      Accept: 'application/json',
    },
  };

  if (/(GET)/.test(method)) {
    newOptions.url = splitUrl(url, { ...options.body });
    delete newOptions.body;
  }

  return axios
    .request(newOptions)
    .then((response) => {
      return response.data;
    })
    .catch((err) => {
      const response = err.response;
      if (response && response.status >= 200 && response.status < 300) {
        return response;
      }
      if (!response) {
        return;
      }
      const status = response.status;
      const errortext = (codeMessage as any)[status] || response.statusText;
      // Notification.error({
      //   message: '',
      //   description: (response.data && response.data.info) || '',
      // });
      const error = new Error(errortext);
      error.name = response.status;
      // error.response = response;
      if (response.data) {
        return response.data;
      }
      throw error;
    });
}
